@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;200;300;400&display=swap);
body {
  margin: 0;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: #333;
}

main {
  background: #111;
  padding: 1rem;
  width: 15rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: "Work Sans", sans-serif;
}

small {
  color: grey;
  min-height: 1rem;
}

header {
  width: 100%;
  text-align: right;
  padding: 1rem;
  color: snow;
  font-size: 1.5rem;
}

#nums-container {
  width: 75%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
#ops-container {
  width: 25%;
}
#clear {
  width: 100%;
  height: 10%;
  border-radius: 50px;
  background: #333;
  color: grey;
  margin-bottom: 1rem;
}

button {
  border-radius: 50%;
  border: none;
  --size: 3rem;
  width: var(--size);
  height: var(--size);
  margin: 0.3rem;
  font-size: 1.5rem;
  cursor: pointer;
}
button:hover {
  opacity: 0.9;
}
button:active {
  -webkit-transform: scale(0.96);
          transform: scale(0.96);
}
button:focus {
  outline: none;
}
button.number {
  background: snow;
}
button.operator {
  background: blue;
  color: white;
}
button.big-h {
  width: 6.6rem;
  border-radius: 50px;
}

